  '我是几年前去过教会的，后来阿妈不给，所以就没继续去了。一直到大概半年前，我在图书馆再次看到传道在邀请新人去教会，问我去不去，我答应了。直到那天起，我就几乎每个星期五星期日都有去教会。没去教会前我是一个有点冷血，自私，和没爱心的人，看到别人欺负弱小的时候我明知道是不对的，可是我害怕会被人迁怒，我胆小，怕他们也会欺负我，所以我就眼睁睁的看着他们欺负别人。我是一个懦弱的人，当初刚到美国不会英文也经常被人欺负，后来慢慢的养成了沉默又安静的性格，在陌生的外国人面前我不敢说话，我怕他们笑我不会英文，又发音不准。',

